import {Select} from '../components/select';
function selectSettings(){
   new Select('#select-maker', {
      placeholder: '',
      data: [
         {id: '1', value: '1'},
         {id: '2', value: '2'},
         {id: '3', value: '4'}
      ]
   });
   new Select('#select-model', {
      placeholder: '',
      data: [
         {id: '1', value: '1'},
         {id: '2', value: '2'},
         {id: '3', value: '3'}
      ],
      disabled: true
   });
   new Select('#select-generation', {
      placeholder: '',
      data: [
         {id: '1', value: '1'},
         {id: '2', value: '2'},
         {id: '3', value: '3'}
      ],
      disabled: true
   });
   new Select('#select-modification', {
      placeholder: '',
      data: [
         {id: '1', value: '1'},
         {id: '2', value: '2'},
         {id: '3', value: '3'}
      ],
      disabled: true
   });
   new Select('#select-car-body', {
      placeholder: '',
      data: [
         {id: '1', value: '1'},
         {id: '2', value: '2'},
         {id: '3', value: '3'}
      ],
      disabled: true
      // selectedId: '3'
   });
}
export default selectSettings;